<template>
  <div id="roleManage">
    <div class="btns_box">
      <Button type="primary" size="small" @click.stop="$refs.userAdd.build = true">新建</Button>
    </div>
    <div class="tab_box">
      <dragTreeTable :data="treeData" :onDrag="onTreeDataChange"></dragTreeTable>
    </div>
    <!--<UserAdd ref="userAdd" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserAdd>
    <UserEdit ref="userEdit" :data="editDefaultData" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserEdit>-->
  </div>
</template>

<script>
import dragTreeTable from '@/components/TreeTable/dragTreeTable'
export default {
  name: 'RoleManage',
  data () {
    return {
      treeData: {
        lists: [
          {
            'id': 40,
            'parent_id': 0,
            'order': 0,
            'name': '红线功能描述',
            sort: 1,
            url: '/main/dataCenter',
            'open': true,
            'lists': []
          }, {
            'id': 5,
            'parent_id': 0,
            'order': 1,
            'name': '调查数据台账',
            sort: 2,
            url: '/main/dataCollection',
            'open': true,
            'lists': [
              {
                'id': 12,
                'parent_id': 5,
                'open': true,
                'order': 0,
                'name': '调查数据信息',
                sort: 1,
                url: '/main/dataCollection/editTable',
                'lists': []
              }
            ]
          },
          {
            'id': 19,
            'parent_id': 0,
            'order': 2,
            'name': '管理系统',
            sort: 3,
            url: '/main/systemManage/menuManage',
            'open': true,
            'lists': []
          }
        ],
        columns: [
          {
            type: 'selection',
            title: '名称',
            field: 'name',
            width: '20%',
            align: 'left',
            formatter: (item) => {
              return '<a style="margin-left: 5px">' + item.name + '</a>'
            }
          },
          {
            type: 'Number',
            title: '排序',
            field: 'sort',
            width: '10%',
            align: 'left'
          },
          {
            type: 'Number',
            title: '请求地址',
            field: 'url',
            width: '40%',
            align: 'left'
          },
          {
            title: '操作',
            type: 'action',
            width: '30%',
            align: 'left',
            actions: [
              {
                text: '查看角色',
                onclick: this.onDetail,
                formatter: (item) => {
                  return '<i style="padding: 0 15px 0 0;color:deepskyblue">查看</i>'
                }
              },
              {
                text: '编辑',
                onclick: this.onEdit,
                formatter: (item) => {
                  return '<i style="padding: 0 15px 0 0;color:deepskyblue">编辑</i>'
                }
              },
              {
                text: '删除',
                onclick: this.onEdit,
                formatter: (item) => {
                  return '<i style="padding: 0 15px 0 0;color:deepskyblue">删除</i>'
                }
              },
              {
                text: '添加下级菜单',
                onclick: this.onEdit,
                formatter: (item) => {
                  return '<i style="padding: 0 15px 0 0;color:deepskyblue">添加下级菜单</i>'
                }
              }
            ]
          }
        ]
      }
    }
  },
  components: {
    dragTreeTable
  },
  mounted () {
  },
  methods: {
    onTreeDataChange (lists) {
      this.treeData.lists = lists
    },
    onDetail () {
    }
  }
}
</script>

<style scoped lang="scss">
  #roleManage{
    .btns_box{
      padding:16px;
      text-align: right;
      border-bottom: 1px solid #d1d1d1;
    }
    .tab_box{
      margin: 10px;
      border: 1px solid #d1d1d1;
    }
    .page_box{
      text-align: right;
      padding:0 10px
    }
  }
</style>
