<template>
    <div class="tree-column" v-bind:style="{ width: width , flex: flex}" v-if="flex">
      <slot></slot>
    </div>
    <div class="tree-column" v-bind:style="{ width: width }" v-else>
      <slot></slot>
    </div>
</template>
<script>
export default {
  name: 'column',
  props: {
    width: String,
    field: String,
    label: String,
    flex: Number
  },
  data () {
    return {
      open: false
    }
  }
}
</script>
<style>
  .tree-column{
    padding: 0 20px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>
